* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100%;
  width: 100%;
  background-color: rgb(232, 232, 206);
  position: relative;
}

/* 首頁頁面*/
.carousel {
  text-align: center;
}
.carousel p {
  font-size: 40px;
}
.slick-slider {
  width: 40vw;
  margin: auto;
  border: 4px dashed rgb(159, 159, 135);
}

.recommend-img {
  width: 200px;
  height: 200px;
}

.coupon-icon {
  width: 10%;
  height: 170px;
  top: 50%;
  position: fixed;
  right: 0px;
  z-index: 2;
}

/*導覽列*/

.navbar {
  width: 100%;
  height: 150px;
  background-color: beige;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}
.navbar .left {
  display: flex;
  align-items: center;
  width: 25%;
}

.left img {
  width: 6.5rem;
  height: 8rem;
  background-color: beige;
}

.center {
  height: 100%;
  width: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-right {
  width: 20rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: beige;
}

.nav-table {
  display: flex;
  align-items: center;
  margin-right: 50px;
  margin-bottom: 30px;
}

.nav-td {
  padding: 0px;
}

.nav-buttons {
  cursor: pointer;
  width: 100px;
  height: 50px;
  border-radius: 35px;
  border: solid 0.3px black;
  background-color: #f58c66;
  font-size: 20px;
}

a.useremail {
  font-size: 20px;
  background-color: beige;
  color: #716f6f;
  text-decoration: none;
}

.nav-actions {
  display: flex;
  justify-content: center;
}

.nav-cart {
  width: 50px;
}

a.useremail:hover {
  color: cornflowerblue;
}

.nav-cart-container {
  position: relative;
}

.cart-number {
  position: absolute;
  top: 0;
  right: 0px;
  background-color: red;
  border-radius: 100%;
  width: 20px;
  text-align: center;
}

/*下拉式選單*/

.drop-down-menu {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background-color: rgb(232, 232, 206);
  list-style: none;
  position: absolute;
  top: 70%;
  left: 30.5%;
}

.drop-down-menu li {
  list-style: none;
  width: 9rem;
  white-space: nowrap;
}

.drop-down-menu > li:last-child {
  border-right: none;
}

.drop-down-menu > li {
  float: left;
}

.drop-down-menu a {
  font-size: 25px;
  text-align: center;
  background-color: rgb(211, 211, 190);
  color: #333;
  display: block;
  padding: 0 30px;
  text-decoration: none;
  line-height: 40px;
}

ul.drop-down-menu li a.border-line {
  border: 0.1px solid #716f6f;
}

a.border-bottom-line {
  border-bottom: 1px solid #b8b5b5;
}

.quantity-button {
  flex: 0 0 auto;
  width: 40px;
  height: 40px;
  margin: 0 10px;
  padding: 0;
  border: 1px solid #ccc;
  background-color: #dddbdb;
  text-align: center;
  font-size: 18px;
  cursor: pointer;
}
.quantity-number {
  font-size: 18px;
  width: 40px;
  text-align: center;
}

ul.drop-down-menu a:hover {
  background-color: #ef5c28;
  color: #fff;
}
ul.drop-down-menu li:hover > a {
  background-color: #ef5c28;
  color: #fff;
}

ul.drop-down-menu ul {
  display: none;
}

ul.drop-down-menu li:hover > ul {
  display: block;
}

