/*商品表格*/

.product-grid-container {
  overflow: auto;
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 20px;
  width: 1200px;
  max-height: 400px;
}

.product-item {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
  background-color: rgb(211, 211, 188);
}

.product-img {
  width: 100%;
  height: auto;
}

.product-name {
  font-weight: bold;
  font-size: 20px;
  margin: 10px 0;
}

.product-border-container {
  border: 1px solid #333;
}

.add-button {
  margin-top: 10px;
  padding: 8px 30px;
  border-radius: 3px;
  box-shadow: 0px 0px 12px -2px rgba(0, 0, 0, 0.5);
  background: #fc6e51;
  user-select: none;
  color: white;
  font-size: 16px;
  letter-spacing: 0.08em;
}

.product-price {
  font-size: 14px;
  color: #333;
}

.money-red {
  color: red;
}

