/*完成訂購頁面*/

.circle-container {
  position: absolute;
  width: 250px;
  height: 250px;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}
.placeorder-text {
  font-size: 40px;
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
  animation: revealCircle 2s ease-in-out forwards;
}
.checkmark {
  font-size: 50px;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  animation: revealCircle 2s ease-in-out forwards;
}

.circle {
  border: 4px solid #4caf50;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0; /* 初始透明 */
  animation: revealCircle 2s ease-in-out forwards;
}

.circle p {
  position: absolute;
}

/* 定義動畫 */
@keyframes revealCircle {
  0% {
    opacity: 0; /* 動畫開始時完全透明 */
  }
  100% {
    opacity: 1; /* 動畫結束時完全顯示 */
  }
}
